import './particles'

particlesJS("particles-dark",
{
    "fps_limit": 30,
    "particles":{
        "number":{
            "value": 80
        },
        "color":{
            "value": "#fff"
        },
        "shape":{
            "type": "circle"
        },
        "opacity":{
            "value":1,
            "random":true,
            "anim":{
                "enable":true,
                "speed":0.5
            }
        },
        "size":{
            "value":1,
            "random":false,
            "anim":{
                "enable": false,
                "speed": 0
            }
        },
        "line_linked":{
            "enabled": true,
            "distance": 100,
            "color":"#fff",
            "width":1
        },
        "move":{
            "enable":true,
            "speed":0.6,
            "direction":"none",
            "straight":false,
            "random":true,
            "out_mode": "bounce"
        }
    },
    "interactivity":{
        "events":{
          "onhover":{
              "enable":true,
              "mode": "bubble"
          },
          "onclick":{
              "enable":false,
              "mode": "push"
          }
        },
        "modes":{
            "repulse":{
                "distance":300,
                "duration":0.8
            },
            "bubble":{
                "distance":300,
                "size":2
            }
        },
        "detect_on": "window"
    }
  });

